import React, {useEffect, useState} from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';

import {BrowserRouter as Router, Redirect, Route, Switch} from "react-router-dom";

import {Layout} from "./hocs/Layout";
import {Home} from "./containers/Home";
import {Login} from "./containers/Login";
import {Signup} from "./containers/SignUp";
import {ResetPassword} from './containers/ResetPassword'
import {API} from "./api/api";
import {Logout} from "./containers/Logout";


const home = "";
const login = home + "/login";
const logout = home + "/logout";
const signup = home + "/signup";
const reset_password = home + "/reset-password";

export const App = () => {

    const [userName, setUser] = useState("");

    useEffect(async () => {
        let user = await API.me(); // {name: ""}
        if (user?.username) {
            setUser(user.username)
        }
    }, []);


    return (
        <Router>
            <Layout userName={userName}>
                <Switch>
                    <Route exact path={home} component={Home}/>
                    <Route exact path={login}>
                        {userName ? <Redirect to={home}/> : () => <Login/>}
                    </Route>
                    <Route exact path={logout}>
                        {!userName ? <Redirect to={login}/> : () => <Logout/>}
                    </Route>
                    <Route exact path={signup}>
                        {userName ? <Redirect to={home}/> : () => <Signup/>}
                    </Route>
                    <Route exact path={reset_password}>
                        {!userName ? <Redirect to={login}/> : () => <ResetPassword/>}
                    </Route>
                </Switch>
            </Layout>
        </Router>
    );
}

